    .hamburger-menu--close .hamburger-menu__bars:after {
      transform: rotate(-90deg); }

.hamburger-menu__bars, .hamburger-menu__bars:before, .hamburger-menu__bars:after {
  width: 35px;
  height: 4px;
  transition: transform .3s;
