.cards {
    display: flex;
    flex-wrap: wrap;
}

.cards__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 30%;
    height: 280px;
    margin-right: rem(20px);
    padding: rem(15px) rem(30px);
    border: 2px solid transparent;
    border-radius: 5px;

    @include screen-md {
        width: 45%;
        padding: rem(5px) rem(20px);
    }

    @include screen-sm {
        width: 90%;
        height: auto;
        border: none;
        flex-direction: row;
        justify-content: flex-start;
        height: auto;
        padding: rem(5px) rem(10px);
    }

    &:nth-child(3n) {
        margin-right: 0;
    }

    &:hover {
        border: 2px solid $accent1;
        box-shadow: 2px 2px 15px 0px gray;

        @include screen-sm {
            border: none;
            box-shadow: none;
        }
    }
}

.card__svg {
    // width: 100px;
    height: 100px;
    fill: $accent2;

    @include screen-sm {
        width: 50px;
        height: 50px;
        flex-shrink: 0;
    }
}

.cards__text {
    text-align: center;

    @include screen-sm {
        text-align: left;
        margin-left: 20px;
    }
}