.menu {
    flex-grow: 1;

    @include screen-sm {

        height: rem(230px);
        transition: .5s;
    }
}

.menu__list {
    display: flex;
    align-items: center;
    height: 100%;

    @include screen-xs {
        flex-wrap: wrap;
        justify-content: center;
    }
}

.menu__item {
    // flex-grow: 1;
    // max-width: 150px;
    // width: 19%;
    justify-content: space-between;
    padding: 10px;
    font-family: $fancy;
    text-transform: uppercase;
    font-size: rem(20px);
    display: flex;
    align-items: center;
    font-weight: 600;
    margin-left: rem(10px);

    @include screen-xs {
        flex-shrink: 0;
    }
}

.menu__link {
    color: $accent2;
    border-bottom: 2px solid transparent;
    padding-bottom: 2px;
    text-align: center;

    &:hover, &:focus {
        border-bottom: 2px solid $accent2;
    }
}