.hamburger-menu
{
    position: relative;
    display: flex;
    width: 35px;
    height: 26px;
    cursor: pointer;
    vertical-align: middle;
    align-items: center;

    &--close {
        .hamburger-menu__bars {
            transform: rotate(45deg);

            &:before {
                transform: rotate(45deg);
                opacity: 0;
            }

            &:after {
                transform: rotate(-90deg);
            }
        }
    }
}

.hamburger-menu__bars {
    &,
    &:before,
    &:after {
        width: 35px;
        height: 4px;
        transition: transform .3s;
        background: $accent1;
    }

    &:before,
    &:after {
        position: absolute;
        display: block;
        content: '';
    }

    &:before {
        top: 0;
    }

    &:after {
        bottom: 0;
    }
}
