.about {
    position: relative;
}

.about__pic {
    position: absolute;
    bottom: rem(20px);
    right: rem(20px);
    fill: $accent2;
    opacity: 0.2;
    height: rem(400px);
    width: rem(400px);

    @include screen-sm {
        height: rem(300px);
        width: rem(300px);
    }
}