.footer {
    display: flex;
    flex-direction: column;
    height: 100%;

    min-height: $minsection;
    @include screen-sm {
        min-height: auto;
    }
}

.footer__info {
    background-color: $footer;
    color: #fff;
    text-align: center;
    padding: rem(40px);

    @include screen-sm {
        padding: rem(10px);
    }
}

.footer__text {
    flex-grow: 1;
    padding: rem(20px) rem(40px);
    display: flex;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    
    @include screen-sm {
        flex-direction: column;
        justify-content: center;
    }
}

.footer__slogan {
    font-family: $fancy;
    font-size: $font-size + 28;
    color: $accent1;
    padding: 0 rem(50px);
    flex-grow: 1;
    text-align: center;

    @include screen-sm {
        font-size: $font-size + 12;
        padding: 0 rem(10px);
    }
}

.footer__map {
    width: 100%;
    height: rem(300px);

    @include screen-sm {
        height: rem(150px);
    }
}