.section {
    width: 90%;
    height: 100%;
    margin: 0 auto;
    max-width: 1200px;
    min-height: $minsection;
    @include screen-sm {
        min-height: auto;
    }
}

.section__title {
    display: inline-block;
    margin: rem(50px) 0 rem(30px) 0;
    font-size: rem(24px);
    text-transform: uppercase;
    color: $accent2;
    font-family: $fancy;

    &:after {
        content: '';
        display: inline-block;
        width: 100%;
        height: 6px;
        background: linear-gradient( to bottom, $accent1, $accent1 50%, $accent2 50%);
    }

    @include screen-sm {
        margin: rem(10px) 0 rem(10px) 0;
    }
}

.section__subtitle {
    margin: 0 0 rem(30px) 0;
    font-size: rem(20px);
    text-transform: uppercase;
    color: $accent2;
    font-family: $fancy;
}