.header__top {
    padding: 0 rem(20px);
    display: flex;
    max-width: 1200px;
    margin: 0 auto;
    position: relative;
}

.header__bottom {
    text-align: center;
    color: #fff;
    height: rem(340px);
    background: url('../img/header.jpg') center center;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @include screen-sm {
        height: rem(200px);
    }

}

.header__menu {
    margin: 0 20px;
    @include screen-sm {
        display: none;
        margin: 0;
    }
}

.header__menu--visible {
    @include screen-sm {
        display: block;
        position: absolute;
        top: rem(80px);
        width: 100%;
        height: auto;
        left: 0;
        right: 0;
        background: #fff;
        overflow: hidden;
        transition: .5s;
    }
}


.header__button {
    margin-top: rem(20px);
    margin-left: auto;
    display: none;
    @include screen-sm {
        display: block;
    }
}