.letters {
    display: flex;
    margin-top: 30px;
}

.letters__letter {
    width: 150px;
    // height: auto;
    margin-right: 20px;
    border: 2px solid transparent;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;

    &:hover {
        border: 2px solid $accent1;
        box-shadow: 2px 2px 15px 0px gray;

        @include screen-sm {
            border: none;
            box-shadow: none;
        }
    }
}