.achievements__achievement {
    display: flex;
    margin-top: rem(50px);

    @include screen-xs {
        margin-top: rem(10px);
    }
}

.achievements__text {
    margin-left: rem(50px);
    padding: 3em 0;

    @include screen-sm {
        margin-left: rem(10px);
        padding: 1em 0;
    }
}

.achievements__image {
    // width: 100px;
    height: rem(200px);
    max-width: rem(100px);
    flex-shrink: 0;
    fill: $accent2;

    @include screen-sm {
        // width: 50px;
        display: none;
    }
}
