.greeting {
    // width: 90%;
    height: 100%;
    margin: 0 auto;
    // max-width: 1200px;
    min-height: $minsection;
    @include screen-sm {
        min-height: auto;
    }
}

.greeting__title {
    display: inline-block;
    font-size: rem(50px);
    text-transform: uppercase;
    color: #fff;
    font-family: $fancy;
    margin: 0;
    padding: 0 rem(10px);

    @include screen-sm {
        font-size: $font-size + 4;
    }
}

.greeting__subtitle {
    display: inline-block;
    font-size: rem(35px);
    font-weight: bold;
    text-transform: uppercase;
    color: #fff;
    font-family: $fancy;
    margin: 0;
    padding: 0 rem(10px);

    @include screen-sm {
        font-size: $font-size;
    }
}

.greeting__text {
    width: 90%;
    margin: rem(50px) auto 0;
    max-width: 1200px;
    font-size: $font-size + 4;

    @include screen-sm {
        font-size: $font-size - 3;
        margin: rem(10px) auto 0;
    }
}