html {
    font-size: $font-size;
    font-family: $regular;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    @include screen-lg {
        $lg : $font-size - 1;
        font-size: rem($lg);
    }

    @include screen-sm {
        $sm : $font-size - 2;
        font-size: rem($sm);
    }

    @include screen-xs {
        $xs : $font-size - 3;
        font-size: rem($xs);
    }
}

body {
  margin: 0;
  padding: 0;

}

*, *::before, *::after { 
    box-sizing: border-box; 
}

body, html {
    height: 100%;
    width: 100%;
}

.wrapper {
    width: 100%; 
    height: 100%;
    position: relative;
    overflow: hidden;
}

.maincontent {
    height: 100%;
    min-height: 800px;
    width: 100%;
    position: absolute;
    // transition: 1s;
    will-change: transform;

    @include screen-sm {
        min-height: auto;
    }
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    text-decoration: none;
    color: $text;
}

.svg {
    width: 100%;
    height: 100%;
}

.img {
    max-width: 100%;
    // height: 100%;
}

p {
    @include screen-sm {
        margin: rem(10px) 0;
    }
}
