.slider {
    width: 100%;
    position: relative;
    padding: 0 rem(25px);
    height: 80%;
}

.swiper-container {
    height: 100%;
}

.slider__arrow {
    position: absolute;
    width: rem(35px);
    height: rem(50px);
    top: 40%;
    fill: $accent1;

    cursor: pointer;

    &--left {
        left: rem(-15px);
        transform: scale(-1);

        @include screen-sm {
            left: rem(-5px);
        }
    }

    &--right {
        right: rem(-15px);

        @include screen-sm {
            right: rem(-5px);
        }
    }
}

.slider__slide {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.slider__title {
    margin-bottom: rem(5px);
    @include screen-xs {
        font-size: 12px;
    }
}

.slider__text {
    @include screen-xs {
        font-size: 10px;
    }
}

.slider__contact {
    color: $accent2;
    font-weight: bold;
    padding: 0 30px;
    @include screen-xs {
        font-size: 10px;
    }
}

.slider__image {
    margin-top: auto;
    width: 100%;
    // height: 50%;
    flex-wrap: nowrap;
    display: flex;
    overflow: hidden;
    justify-content: center;

    img {
        flex-shrink: 1;
    }
}
