.clients {
    position: relative;
}

.clients__list  {
    display: flex;
    // height: rem(200px);
    width: 100%;
    flex-wrap: wrap;
    align-content: center;
    margin-bottom: rem(30px);
}

.clients__item {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    width: 25%;
    color: $accent2;
    font-weight: bold;
    padding: 1em 0;

    @include screen-sm {
        width: 33%;
    }

    @include screen-xs {
        width: 50%;
        padding: 0.5em 0;
    }
}
.clients__title {
    margin-top: rem(50px);
}