.dots {
    position: fixed;
    top: 97px;
    right: 37px;

    @include screen-sm {
        display: none;
    }
}

.dots__list {
    margin-left: 30px;
}

.dots__link {
    width: 16px;
    height: 16px;
    display: block;
    position: relative;

    &:after, &:before {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
    }

    &:after {
        width: 5px;
        height: 5px;
        background: $accent1;
    }

    &:before {
        width: 0px;
        height: 0px;
        border: 1px solid $accent1;
        opacity: 0;
        transition: 0.3s;
    }

    &:hover {
        &:before {
            width: 14px;
            height: 14px;
            opacity: 1;
        }
    }

    &_active {
        &:before {
            width: 14px;
            height: 14px;
            opacity: 1;
        }
    }
}

.dots__item {
    margin-bottom: 5px;
}